<template>
    <ol-map
        :loadTilesWhileAnimating="true"
        :loadTilesWhileInteracting="true"
        :style="mStyle"
    >
        <ol-view
        ref="view"
        :center="center"
        :zoom="zoom"
        :projection="projection"
        />

        <ol-tile-layer> <ol-source-osm /></ol-tile-layer>

    </ol-map>
</template>

<script>
import { ref } from "vue";

export default {
    props:{
        height:String,
        width:String,
    },

    setup() {
        
    },

    data() {
        return {
            mStyle: 'width:'+this.width+'; height: '+this.height,
            center: [126.58, 33.38 ], 
            zoom: 10,
            projection:"EPSG:4326",
        }
    }
}
</script>

