<template>
    <div id="map"></div>
    <div class="bot1">
        <b-button pill variant="primary" @click="displayOleum"></b-button>
        <b-button pill variant="primary" @click="displayOlle"></b-button>
        <b-button pill variant="primary" @click="displayHalla"> </b-button>
        <b-button pill variant="primary" @click="displayHiking"></b-button>
        <b-button pill variant="primary" @click="displayzpt"></b-button>
    </div>
    <div class="bot2">
        <b-button pill variant="success" @click="displayJejuCafe"></b-button>
        <b-button pill variant="success" @click="displayJejuRest"></b-button>
        <b-button pill variant="success" @click="displaySeogwipoCafe"></b-button>
        <b-button pill variant="success" @click="displaySeogwipoRest"></b-button>
        <b-button pill variant="success" @click="displayHotel('all')"></b-button>
    </div>
    <div class="bot3">
        <b-button pill variant="secondary" @click="getdataFromkakao"> </b-button>
        <b-button pill variant="secondary" @click="kakaodatamyfileWrite">  </b-button>
    </div>

</template>

<script>
import { toRaw } from "vue";
import {ref, watch, watchEffect} from 'vue';

import oleum_data from "../assets/jeju_oleum.json"
import olle_data from "../assets/jeju_olle_trail.json"
import halla_data from "../assets/jeju_halla_trail.json"
import hiking_data from "../assets/jeju_hiking_total.json"
import zpt_data from "../assets/jeju_zpt.json"

import jeju_rest_all_data from "../assets/jeju_rest_all.json"
import jeju_cafe_all_data from "../assets/jeju_cafe_all.json"
import hotel_all_data from "../assets/jeju_hotel_all.json"

var apikey= ref('');

var customOverlay=({});
var infoWindow=({});
var cataindex=0;
var hallaIndex=true;
var halla_polylines=[];
var olleIndex=true;
var olle_polylines=[];
var hikingIndex=true;
var hike_polylines=[];
var zptIndex=true;
var zpt_polylines=[];
var oleumIndex=true;
var oleum_circles=[];

var jejucafeIndex=true;
var jejucafeMarkers=[];
var jejurestIndex=true;
var jejurestMarkers=[];
var seogwipocafeIndex=true;
var seogwipocafeMarkers=[];
var seogwiporestIndex=true;
var seogwiporestMarkers=[];

var hotelIndex=true;
var hotelMarkers=[];

var searchData=[];
var changeMaptype;
var currentTypeId;

export default {
    name: "KakaoMap",
    
    props:{
        height:String,
        width:String,
    },

    data() {
        return {            
        };
    },

    created() {
        apikey = process.env.VUE_APP_KAKAOMAP_API_KEY;        
        // console.log('api_key ', apikey);
    },

    mounted() {
        if (!window.kakao || !window.kakao.maps) {            
            const script = document.createElement("script");
            script.onload = () => kakao.maps.load(this.initMap);
            script.src ="//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey="+apikey+'&libraries=services';
            document.head.appendChild(script);
        } else {
            this.initMap();            
        }            
    },

    methods: {

        initMap() {

            const container = document.getElementById("map");           
            container.style.width = `${this.width}`;
            container.style.height = `${this.height}`;            
            const options = {
                center: new kakao.maps.LatLng(33.38, 126.58),
                level: 10,
            };
            this.map = new kakao.maps.Map(container, options), 
            customOverlay = new kakao.maps.CustomOverlay({}),
            infoWindow = new kakao.maps.InfoWindow({});
            this.map.relayout();

            var mapTypeControl = new kakao.maps.MapTypeControl();
            this.map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);
            var zoomControl = new kakao.maps.ZoomControl();
            this.map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

            var content = document.createElement('div');
            content.className = 'modes';
            content.innerHTML =  
                // '<div class="modes">\n'+
                // '<button id="hybrid"></button>\n' +
                '<button id="terrain"></button>\n' +
                '<button id="traffic"></button>\n' +
                '<button id="roadview"></button>\n' +
                '<button id="use_district"></button>\n' +
                '<button id="jejusi"></button>\n' +
                '<button id="seogwiposi"></button>\n' +
                '</div>\n';
            container.appendChild(content);              
            // console.log(document);

            // const hybrid_btn = document.querySelector('#hybrid');  
            // hybrid_btn.addEventListener('click', (event) => {
            //     this.onSelectMap('hybrid')
            // });

            const terrain_btn = document.querySelector('#terrain');  
            terrain_btn.addEventListener('click', (event) => {
                this.onSelectMap('terrain')
            });

            const traffic_btn = document.querySelector('#traffic');  
            traffic_btn.addEventListener('click', (event) => {
                this.onSelectMap('traffic')
            });

            const roadview_btn = document.querySelector('#roadview');  
            roadview_btn.addEventListener('click', (event) => {
                this.onSelectMap('roadview')
            });

            const use_district_btn = document.querySelector('#use_district');  
            use_district_btn.addEventListener('click', (event) => {
                this.onSelectMap('use_district')
            });

            const jejusi_btn = document.querySelector('#jejusi');  
            jejusi_btn.addEventListener('click', (event) => {
                this.displayHotel('')
            });
            const seogwiposi_btn = document.querySelector('#seogwiposi');  
            seogwiposi_btn.addEventListener('click', (event) => {
                this.displayHotel('')
            });

            // console.log(div);
            // // toRaw(this.map).relayout();
        },

        onSelectMap: function(maptype) {
            if(maptype ==='roadmap') {
                if (currentTypeId) {
                    this.map.removeOverlayMapTypeId(currentTypeId);    
                }
                this.map.setMapTypeId(kakao.maps.MapTypeId.ROADMAP)
            } 
            // else if (maptype ==='hybrid') {
            //     this.map.setMapTypeId(kakao.maps.MapTypeId.HYBRID) 
            // } 
            else {
                
                if (maptype === 'traffic') {
                    changeMaptype = kakao.maps.MapTypeId.TRAFFIC;
                } else if (maptype === 'roadview') {
                    changeMaptype = kakao.maps.MapTypeId.ROADVIEW;
                } else if (maptype === 'terrain') {
                    changeMaptype = kakao.maps.MapTypeId.TERRAIN;
                } else if (maptype === 'use_district') {
                    changeMaptype = kakao.maps.MapTypeId.USE_DISTRICT;           
                }
                if (currentTypeId) {
                    this.map.removeOverlayMapTypeId(currentTypeId);    
                }
                this.map.addOverlayMapTypeId(changeMaptype);
                currentTypeId = changeMaptype;
            }
            
            // else if (maptype ==='terrain') {
            //     this.map.addOverlayMapTypeId(kakao.maps.MapTypeId.TERRAIN)
            //     this.map.removeOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC) 
            // } else if (maptype ==='traffic') {
            //     // const container = document.getElementById("map"); 
            //     // const now_center = this.map.getCenter()
            //     // const options = {
            //     //     center: new kakao.maps.LatLng(now_center.Ma, now_center.La),
            //     //     level: 5,
            //     // };
            //     // this.map = new kakao.maps.Map(container, options) 
            //     this.map.addOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC)
            // }            
        },

        displayOleum() {

            cataindex=1;
            if(oleumIndex) {

                if(oleum_circles.length <=0 ) {
                    let features = oleum_data.features;
                    features.forEach( element => {
                        let position = new kakao.maps.LatLng(element.geometry.coordinates[1], element.geometry.coordinates[0]);
                        let dia = element.properties['']; 
                        let name = element.properties['']
                        this.drawingCircle(cataindex, toRaw(this.map), position, dia, name);                        
                    });                    
                } else {
                    oleum_circles.map( (cur) => cur.setMap(toRaw(this.map)));
                }
                oleumIndex = false;
            } else {
                oleum_circles.map( (cur) => cur.setMap(null));
                oleumIndex = true;
            }
            
        },

        displayOlle() {

            cataindex=1;
            if(olleIndex) {
                if( olle_polylines.length <=0 ) {
                    let features = olle_data.features;            
                    features.forEach( element => {      
                        let name = element.properties.name;             
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex,toRaw(this.map), path, name);
                        });
                    });
                } else {
                    olle_polylines.map( (cur) => cur.setMap(toRaw(this.map)));
                }                
                olleIndex = false;
            } else {
                olle_polylines.map( (cur) => cur.setMap(null));
                olleIndex = true;
            }            
        },

        displayHalla() {

            cataindex=2; 
            if(hallaIndex) {
                if ( halla_polylines.length <=0 ) {
                    let features = halla_data.features;            
                    features.forEach( element => { 
                        let name = element.properties.name;  
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);                     
                        });
                    });
                } else {
                    halla_polylines.map( (cur) => cur.setMap(toRaw(this.map)));
                }
                hallaIndex = false;
            } else {
                halla_polylines.map( (cur) => cur.setMap(null));
                hallaIndex = true;
            }
            
        },

        displayHiking() {

            cataindex=3;
            if(hikingIndex){
                if ( hike_polylines.length <=0 ) {                    
                    let features = hiking_data.features;
                    features.forEach( element => {                        
                        let name = ' '+ element.properties.PMNTN_NM;                
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);            
                        });
                    });
                } else {
                    hike_polylines.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                hikingIndex = false;
            } else {
                hike_polylines.map( (cur) => cur.setMap(null));
                hikingIndex = true;
            }          
        },

        displayzpt(){

            cataindex=4;
            if(zptIndex){
                if ( zpt_polylines.length <=0 ) {                    
                    let features = zpt_data.features;
                    features.forEach( element => {                        
                        let name = element.properties.name;                
                        let coord = element.geometry.coordinates;

                        if ( element.geometry.type=='LineString' ) {                            
                            
                            var path=[];                            
                            coord.forEach( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);              
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);

                        } else if (element.geometry.type=='MultiLineString'){
                            
                            coord.forEach( points => {
                                var path=[];
                                points.forEach ( point => {
                                    let position = new kakao.maps.LatLng(point[1], point[0]);
                                    path.push(position);
                                });
                                this.drawingPolyline(cataindex, toRaw(this.map), path, name);            
                            });

                        }
                    });
                } else {
                    zpt_polylines.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                zptIndex = false;
            } else {
                zpt_polylines.map( (cur) => cur.setMap(null));
                zptIndex = true;
            }

        },

        drawingCircle(cataindex, map, position, dia, name) {
            
            let fillrealcolor='';
            let backcolor='';
            
            if (cataindex==1) {
                fillrealcolor = '#E51D1A';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                fillrealcolor = '#00FF0F';
                backcolor = '#FF0000';      
            } else if (cataindex==3) { 
                fillrealcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                fillrealcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            var circle = new kakao.maps.Circle({
                    map: map,
                    center : position,
                    radius: dia,    
                    strokeColor: '#5347AA',
                    strokeOpacity: 0.5,
                    strokeWeight: 2,
                    fillColor: fillrealcolor,
                    fillOpacity: 0.3,
                    //    
                    // radius: 5000, //     
                    // strokeWeight: 5, //   
                    // strokeColor: '#75B8FA', //  
                    // strokeOpacity: 1, //    1 0   0  
                    // strokeStyle: 'dashed', //   
                    // fillColor: '#CFE7FF', //  
                    // fillOpacity: 0.7  //      
            });           
            
            var infoBox = new kakao.maps.CustomOverlay({
                content: name + '  [ '+dia+' m]',
                yAnchor: 1.5,
            });

            new kakao.maps.event.addListener(circle, 'mouseover', function(mouseEvent) {                                  
                circle.setOptions({
                    fillOpacity: 0.6,
                    strokeStyle: 'dashed'
                });
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            })
            
            new kakao.maps.event.addListener(circle, 'mousemove', function(mouseEvent) {                
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            });
            
            new kakao.maps.event.addListener(circle, 'mouseout', function() {                
                circle.setOptions({
                    fillOpacity: 0.3,
                    strokeStyle: 'line'
                });
                infoBox.setMap(null);   
            });

            if (cataindex==1) {
                oleum_circles.push(circle);
            } 
            // else if (cataindex==2) {
            //     halla_polylines.push(polyline);
            // } else if (cataindex==3) { 
            //     hike_polylines.push(polyline);
            // } else if (cataindex==4) { 
            //     zpt_polylines.push(polyline);
            // }  
        },

        drawingPolyline(cataindex, map, path, name) {

            let realcolor='';
            let backcolor='';

            if (cataindex==1) {
                realcolor = '#0004FF';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                realcolor = '#00FF0F';
                backcolor = '#FF0000';      
            } else if (cataindex==3) { 
                realcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                realcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            var polyline = new kakao.maps.Polyline({
                map: map,
                path: path, //    
                strokeWeight: 2, //   
                strokeColor: realcolor, //  
                strokeOpacity: 0.5, //    1 0   0  
                strokeStyle: 'solid'                 
            });            
            
            var infoBox = new kakao.maps.CustomOverlay({
                content: name,
                yAnchor: 1,
            });

            new kakao.maps.event.addListener(polyline, 'mouseover', function(mouseEvent) {                                  
                polyline.setOptions({
                    strokeWeight: 7,
                    strokeColor: backcolor,
                    strokeOpacity: 1,
                    strokeStyle: 'solid'
                });
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
                // console.log('ddd ', infoBox);
            })
            
            new kakao.maps.event.addListener(polyline, 'mousemove', function(mouseEvent) {                
                // customOverlay.setPosition(mouseEvent.latLng); 
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            });
            
            new kakao.maps.event.addListener(polyline, 'mouseout', function() {                
                polyline.setOptions({
                    strokeWeight: 2, //   
                    strokeColor: realcolor, //  
                    strokeOpacity: 0.5, //    1 0   0  
                    strokeStyle: 'solid' 
                })
                infoBox.setMap(null);   
            });

            if (cataindex==1) {
                olle_polylines.push(polyline);
            } else if (cataindex==2) {
                halla_polylines.push(polyline);
            } else if (cataindex==3) { 
                hike_polylines.push(polyline);
            } else if (cataindex==4) { 
                zpt_polylines.push(polyline);
            }  
        },

        displayJejuCafe(){  
            cataindex=10;          
            if(jejucafeIndex){
                if ( jejucafeMarkers.length <=0 ) {
                    
                    let features = jeju_cafe_all_data.features;
                    features.forEach( element => {
                        // console.log(element)   
                        let name = element.place_name;                
                        let my_address = element.address_name;
                        let city = element.address_name.split(' ')[1];
                        let my_link = element.place_url;
                        var position = new kakao.maps.LatLng(element.y, element.x);
                        let phone = element.phone;                        
                        if( city=='') {
                            this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone);
                        }                        
                    });
                    
                    // let features = jejucafe_data.features;
                    // features.forEach( element => {                        
                    //     let name = element.properties.Name;                
                    //     let my_point = element.geometry.coordinates;
                    //     let my_address = element.properties.address;
                    //     let my_link = element.properties.link;
                    //     let my_layer = element.properties.layer;
                    //     var position = new kakao.maps.LatLng(my_point[1], my_point[0]);                        
                    //     if( my_layer==' ') {                            
                    //         this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link);
                    //     }                        
                    // });
                } else {
                    jejucafeMarkers.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                jejucafeIndex = false;
            } else {
                jejucafeMarkers.map( (cur) => cur.setMap(null));
                jejucafeIndex = true;
            }
        },

        displayJejuRest(){
            cataindex=11;
            if(jejurestIndex){
                if ( jejurestMarkers.length <=0 ) {                    
                    let features = jeju_rest_all_data.features;
                    features.forEach( element => {   
                        let name = element.place_name;                
                        let my_address = element.address_name;
                        let city = element.address_name.split(' ')[1];
                        let my_link = element.place_url;
                        var position = new kakao.maps.LatLng(element.y, element.x);
                        let phone = element.phone;                        
                        if( city=='') {
                            this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone);
                        }                        
                    });
                    // features.forEach( element => {   
                    //     // console.log(element)
                    //     let name = element.place_name;                
                    //     let my_address = element.address_name;
                    //     let my_link = element.place_url;
                    //     // let my_layer = element.id;
                    //     var position = new kakao.maps.LatLng(element.y, element.x);                        
                    //     // if( my_layer==' ') {
                    //         this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link);
                    //     // }                        
                    // });
                } else {
                    jejurestMarkers.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                jejurestIndex = false;
            } else {
                jejurestMarkers.map( (cur) => cur.setMap(null));
                jejurestIndex = true;
            }
        },

        displaySeogwipoCafe(){  
            cataindex=12;          
            if(seogwipocafeIndex){
                if ( seogwipocafeMarkers.length <=0 ) {                    
                    let features = jeju_cafe_all_data.features;
                    features.forEach( element => {   
                        let name = element.place_name;                
                        let my_address = element.address_name;
                        let city = element.address_name.split(' ')[1];
                        let my_link = element.place_url;
                        var position = new kakao.maps.LatLng(element.y, element.x);
                        let phone = element.phone;                        
                        if( city=='') {
                            this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone);
                        }                        
                    });

                    // let features = jejucafe_data.features;
                    // features.forEach( element => {                        
                    //     let name = element.properties.Name;                
                    //     let my_point = element.geometry.coordinates;
                    //     let my_address = element.properties.address;
                    //     let my_link = element.properties.link;
                    //     let my_layer = element.properties.layer;
                    //     var position = new kakao.maps.LatLng(my_point[1], my_point[0]);                        
                    //     if( my_layer==' ') {                            
                    //         this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link);
                    //     }                        
                    // });
                } else {
                    seogwipocafeMarkers.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                seogwipocafeIndex = false;
            } else {
                seogwipocafeMarkers.map( (cur) => cur.setMap(null));
                seogwipocafeIndex = true;
            }
        },

        displaySeogwipoRest(){
            cataindex=13;
            if(seogwiporestIndex){
                if ( seogwiporestMarkers.length <=0 ) {
                    let features = jeju_rest_all_data.features;
                    features.forEach( element => {   
                        let name = element.place_name;                
                        let my_address = element.address_name;
                        let city = element.address_name.split(' ')[1];
                        let my_link = element.place_url;
                        var position = new kakao.maps.LatLng(element.y, element.x);
                        let phone = element.phone;                        
                        if( city=='') {
                            this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone);
                        }                        
                    });                    
                    // let features = jejucafe_data.features;
                    // features.forEach( element => {                        
                    //     let name = element.properties.Name;                
                    //     let my_point = element.geometry.coordinates;
                    //     let my_address = element.properties.address;
                    //     let my_link = element.properties.link;
                    //     let my_layer = element.properties.layer;
                    //     var position = new kakao.maps.LatLng(my_point[1], my_point[0]);                        
                    //     if( my_layer==' ') {
                    //         this.drawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link);
                    //     }                        
                    // });
                } else {
                    seogwiporestMarkers.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                seogwiporestIndex = false;
            } else {
                seogwiporestMarkers.map( (cur) => cur.setMap(null));
                seogwiporestIndex = true;
            }
        },

        drawingMarker(cataindex, map, position, name, my_address, my_link, phone) {

            // var position = new kakao.maps.LatLng(my_point[1], my_point[0]);
            // console.log( cataindex, my_address, my_link);
            var imageSrc = '';
            if( cataindex ==10) {
                imageSrc = '/img/jeju_cafe.png';
            } else if (cataindex ==11) {
                imageSrc = '/img/jeju_rest.png';
            } else if (cataindex ==12) {
                imageSrc = '/img/seogwipo_cafe.png';
            } else if (cataindex ==13) {
                imageSrc = '/img/seogwipo_rest.png';
            } else if (cataindex ==14) {
                imageSrc = '/img/seogwipo_cafe.png';
            }

            var imageSize = new kakao.maps.Size(16, 17);
            var imageOption = {offset: new kakao.maps.Point(8, 9)};            
            var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption)
            var marker = new kakao.maps.Marker({
                position : position,
                clickable: true,
                image : markerImage,
                map : map,
                title : name,
            });


            // var content =  '<div style="width:250px; height: 150px; padding: 5px;">';
